
// Vue
import { defineComponent, computed, ref } from 'vue'
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'
// Interfaces & Types
import { TodosGettersEnum } from '@/types/todos/types'
import { CategoriesGettersEnum } from '@/types/categories/types'
// Component
import PageHeader from '@/components/elements/content/Header/index.vue'
import PageGreeting from '@/components/elements/content/Greeting/index.vue'
import ChildPageControl from '@/components/elements/content/ChildPageControl/index.vue'
import CategoryTodos from '@/components/elements/content/CategoryTodos/index.vue'

export default defineComponent({
  name: 'Dashboard',
  components: {
    PageHeader,
    PageGreeting,
    ChildPageControl,
    CategoryTodos
  },
  setup () {
    /**
     * Context
     */
    const store = useStore()
    const route = useRoute()

    /**
     * Data
     */
    // # Default child pages data
    const childPages = [
      { to: '/', name: 'All' },
      { to: '/?key=daily', name: 'Daily overview' },
      { to: '/statistic', name: 'Statistics' }
    ]
    // ––––––––––––––––––––
    // Categories
    const categories = computed(() => store.getters[CategoriesGettersEnum.GetAllCategories])
    // Todos type
    const isDailyTodos = computed<boolean>(() => route.query.key === 'daily')
    // View completed todos
    const viewCompletedTodos = ref<boolean>(false)
    // Active todos
    const activeTodos = computed(() => isDailyTodos.value ? store.getters[TodosGettersEnum.GetActiveTodosByDate]('2021-04-14') : store.getters[TodosGettersEnum.GetActiveTodos])
    // Completed todos
    const completedTodos = computed(() => viewCompletedTodos.value ? store.getters[TodosGettersEnum.GetCompletedTodos] : [])

    return {
      // Data
      categories,
      childPages,
      viewCompletedTodos,
      activeTodos,
      completedTodos
    }
  }
});
