<div class="category_todo_wrapper" v-if="activeTodos.length &gt; 0 || (activeTodos.length === 0 &amp;&amp; emptyViewStatus === true)"><Header :icon="category.icon" :color="category.color" :title="category.title" :viewStatus="viewContentStatus" :event="toggleViewContentStatus"></Header><Content v-if="viewContentStatus" :todos="activeTodos" :color="category.color"></Content><Footer :id="category.id"></Footer></div>