
// Vue
import { computed, defineComponent } from 'vue'
import { useRoute } from 'vue-router'

export default defineComponent({
  name: 'ChildPageControl',
  props: {
    /**
     * Links array
     */
    links: {
      type: Array,
      required: false,
      default: () => []
    }
  },
  setup () {
    /**
     * Context
     */
    const route = useRoute()

    /**
     * Data
     */
    // # Route current path
    const routeCurrentPath = computed<any>(() => route.fullPath)

    /**
     * Return
     */
    return {
      // Data
      routeCurrentPath
    }
  }
})
